

























.navs {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: #fff;
  -webkit-box-shadow: 0 2px 4px #000;
          box-shadow: 0 2px 4px #000;
  width: 100%;
}
.navs > ul,
.navs > ul > li {
  margin: 0;
  padding: 0;
}
.navs ul {
  display: table;
  width: 100%;
}
.navs ul > li {
  text-align: center;
  font-size: 10px;
  display: table-cell;
  padding: 18px 12px;
  cursor: pointer;
}
.navs ul > li.active {
  color: #D0021B;
}


















